.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
    z-index: 99999;
    
  }
 
  .nav-menu {
    background-color: #1B57A6;
    width: 230px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 0.8s;
    z-index: 99999;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
    z-index: 99999;
  }
  
  .nav-text {
    display: flex;
    align-items: center;
    padding: 5px 0px 0px 5px;
    list-style: none;
    width: 100%;
    height: 80px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: left;
    padding: 0 0px;
    border-radius: 4px;
  }
  
  .nav-text a:hover{
    background-color: #002555;;
    color: white;
  }

  .icon{
    color: rgb(255, 255, 255);
  }

  .nav-text a:hover .icon{
    color: rgb(0, 0, 0);
  }
  
  .nav-menu-items {
    width: 100%;
    flex-direction: column;
  }
  
  .navbar-toggle {
